.contacts-f-formGroup {
  width: 100%;
  max-width: 530px;
}

.contacts-f-label {
  position: relative;
  display: inline-block;
  width: 100%;
  font-weight: 500;
}

.contacts-f-label ~ .contacts-f-label {
  margin-top: 36px;
}

.contacts-f-label:nth-child(4) {
  margin-bottom: 49px;
}

.contacts-f-field {
  width: 100%;
  height: 75px;
  margin-top: 22px;
  border: 1px solid var(--text-grey-4);
  border-radius: 10px;
  padding: 25px 31px;
  transition: all 0.25s ease;
  cursor: pointer;
}

.contacts-f-field:hover {
  box-shadow: var(--hover-second-btn);
}
