.footerList {
  list-style-type: none;
  text-transform: capitalize;
}

.footerItem + .footerItem {
  margin-top: 46px;
}

.footerItem > a {
  text-decoration: none;
  font-weight: 500;
  color: inherit;
  transition: all 0.25s linear;
}

.footerItem > a:hover {
  text-decoration: underline;
}

.linkTitle {
  color: var(--text-grey-4);
  font-weight: 500;
}
