.modalNotifyContainer {
  width: 100%;
  text-align: center;
  padding: 40px 15px;
  background: linear-gradient(-45deg, #ee7752, #23a6d5, #b88e2f, #2ec1ac);
  background-size: 200% 200%;
  background-position: 0 0;
  color: white;
  font-family: Montserrat, sans-serif;
  letter-spacing: 1.2;
  animation: gradient 15s ease infinite;
}

.modalNotifyButton {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  cursor: pointer;
}

.modalNotifyButton > svg {
  width: 100%;
  height: 100%;
  fill: white;
}

.modalNotifyButton:hover,
.modalNotifyButton:focus {
  box-shadow: var(--hover-second-btn);
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
