.cl-list {
  display: flex;
  align-items: center;
  max-width: 817px;
  padding: 15px 0;
  background-color: var(--bg-secondary);
  list-style-type: none;
  font-weight: 500;
}

.cl-list > li:first-child {
  width: 100%;
  max-width: 70px;
  text-align: right;
  flex-shrink: 0;
}

.cl-list > li:nth-child(2) {
  width: 100%;
  max-width: 154px;
  padding-left: 5px;
  text-align: right;
}

.cl-list > li:nth-child(3) {
  width: 100%;
  max-width: 207px;
  padding-left: 5px;
  text-align: right;
}

.cl-list > li:nth-child(4) {
  width: 100%;
  max-width: 215px;
  padding-left: 36px;
  text-align: left;
}

@media screen and (min-width: 500px) {
  .cl-list > li:first-child {
    max-width: 205px;
  }
}
