.filterBar-section {
  position: relative;
  width: 100%;
  z-index: 20;
}

.fiterBar-icon {
  width: 100%;
  height: 100%;
}

.filterBar-container {
  position: relative;
  flex-direction: column-reverse;
  gap: 17px;
  width: 100%;
  max-width: 1440px;
  min-height: 100px;
  margin: 0 auto;
  padding: 10px;
  background-color: var(--bg-secondary);
}

.filterBar-container button {
  width: 30px;
  height: 30px;
  cursor: pointer;
  border-radius: 50%;
  padding: 5px;
}

.filterBar-filterItem {
  gap: 14px;
  text-transform: capitalize;
}

.filterBar-gridBtn {
  margin-left: 23px;
}

.filterBar-gridBtn + button {
  margin-left: 24px;
}

.filterBar-divider {
  height: 37px;
  width: 1px;
  margin-left: 30px;
  margin-right: 34px;
  background-color: var(--text-grey-4);
}

.filterBar-fieldContainer {
  width: 100%;
}

.filterBar-field {
  width: 55px;
  min-height: 55px;
  font-size: 20px;
  color: var(--text-grey-4);
  padding: 12px 17px;
  margin-left: 17px;
  border: none;
}

.filterBar-field::-webkit-outer-spin-button,
.filterBar-field::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.filterBar-field:focus {
  outline: none;
  border: none;
}

.filterBar-selectContainer {
  margin-left: 29px;
  gap: 17px;
}

.filterBar-selectContainer > span {
  text-wrap: nowrap;
}

.filterBar-select {
  width: 100vw;
  max-width: 188px;
  min-height: 55px;
  padding: 12px 5px 12px 30px;
  border: none;
  cursor: pointer;

  font-size: 20px;
  color: var(--text-grey-4);
}

.filterBtnActive {
  background-color: #f5e5d1;
  box-shadow:
    inset rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

@media screen and (min-width: 980px) {
  .filterBar-container {
    flex-direction: row;
  }
}

@media screen and (min-width: 1440px) {
  .filterBar-container {
    padding: 0 100px;
  }
}
