.itemDescribeContainer > h1 {
  font-size: 42px;
  font-weight: 400;
  text-align: center;
}

.itemDescSubtitle {
  margin-bottom: 10px;

  font-size: 24px;
  font-weight: 500;
  color: var(--text-grey-4);
}

.itemRatingRow {
  display: flex;
  align-items: center;
}

.itemRatingRow_rating {
  margin: 0 18px 0 0;
}

.divider {
  width: 1px;
  height: 30px;
  background-color: var(--text-grey-4);
}

.itemRatingRow > button[name='divider'] {
  margin-left: 22px;

  font-size: 13px;
  color: var(--text-grey-4);
  cursor: pointer;
  transition: all 0.25s ease;
}

.itemRatingRow > button[name='divider']:hover,
.itemRatingRow > button[name='divider']:focus {
  font-weight: 500;
  letter-spacing: 0.2px;
}

.itemDescribeText {
  width: 100%;
  height: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 13px;
  margin-bottom: 22px;

  font-size: 13px;
}

@media screen and (min-width: 980px) {
  .itemDescribeContainer > h1 {
    text-align: left;
  }
  .itemDescribeText {
    max-width: 424px;
  }
}
