.slideBtnContainer {
  display: inline-block;
}

.slideBtnContainer > input {
  position: absolute;
  padding: 0;
  margin: 0;
  visibility: hidden;
  width: 1px;
  height: 1px;
  z-index: -1;
}

.slideBtnContainer > label {
  position: relative;
  display: inline-block;
  width: 11px;
  height: 11px;
  margin: 10px;
  background-color: var(--text-grey-5);
  border-radius: 50%;
  z-index: 1;
  cursor: pointer;
}

.slideBtnContainer > label:before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 27px;
  height: 27px;
  background: none;
  border-radius: 50%;
  transition: background 0.35s linear;
}

.slideBtnContainer > input:checked + label::before {
  background: center/cover no-repeat url(../../../../../images/svg/active.svg)
    white;
}
