.checkoutContainer {
  width: 100%;
  max-width: 608px;
  padding: 35px 79px 71px 75px;
}

.checkoutTitle {
  font-size: 36px;
  font-weight: 600;
  margin-bottom: 36px;
}

.checkoutForm,
.checkoutForm > div {
  width: 100%;
}

.checkoutFormDivider {
  display: flex;
  gap: 31px;
}

.checkoutForm > div {
  display: flex;
  flex-direction: column;
  gap: 36px;
}

.checkoutLabel {
  position: relative;
  width: 100%;
  font-weight: 500;
  text-transform: capitalize;
}
.checkoutLabel > span {
  display: inline-block;
  margin-bottom: 22px;
}

.checkoutField {
  width: 100%;
  height: 75px;
  padding: 0 31px;
  border: 1px solid var(--text-grey-4);
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s linear;
}

.checkoutField:hover,
.checkoutField:focus {
  box-shadow: var(--hover-second-btn);
}
